import * as React from 'react';
// import { Breadcrumb } from 'gatsby-plugin-breadcrumb';
import Header from './header';
import Footer from './footer';
import SideBar from './sideBar';
import * as style from '../styles/style.module.css';

const Layout = ({ pageType, pageTitle, children }) => {
  const stylePage = pageType ? style[pageType] : style.pages;
  return (
    <>
      <Header />
      {/* <Breadcrumb location={location} crumbLabel={pageTitle} /> */}
      <div className={`${style.container} ${style.wrapper}`}>
        <div className={stylePage}>
          <main className={style.main}>
            <article>
              <header>
                <h1 className={style.pageTitle}>{pageTitle}</h1>
              </header>
              {children}
            </article>
          </main>
          <aside className={style.sideBar}>
            <SideBar />
          </aside>
        </div>
      </div>
      <Footer />
    </>
  )
};

export default Layout;