import * as React from 'react';
import PropTypes from 'prop-types';
import kebabCase from 'lodash/kebabCase';
import { Link, graphql, useStaticQuery } from 'gatsby';
import * as style from '../styles/style.module.css';

const WidgetTags = () => {
  const query = useStaticQuery(graphql`
    query {
      allMdx(limit: 100) {
        group(field: frontmatter___tags) {
          fieldValue
          totalCount
        }
      }
    }
  `);
  const group = query.allMdx.group.sort(
    (a, b) => b.totalCount - a.totalCount
  );
  return (
    <ul className={style.tagCroud}>
      {group.map(tag => (
        <li key={tag.fieldValue} className={style.tagCroud__item}>
          <Link to={`/tag-list/${kebabCase(tag.fieldValue)}/`} className={style.tagCroud__link}>
            {tag.fieldValue} ({tag.totalCount})
          </Link>
        </li>
      ))}
    </ul>
  )
};

WidgetTags.propTypes = {
  data: PropTypes.shape({
    allMdx: PropTypes.shape({
      group: PropTypes.arrayOf(
        PropTypes.shape({
          fieldValue: PropTypes.string.isRequired,
          totalCount: PropTypes.number.isRequired,
        }).isRequired
      ),
    }),
  }),
};

export default WidgetTags;
