import * as React from 'react';
import { useStaticQuery, graphql, Link } from 'gatsby';
import { StaticImage } from 'gatsby-plugin-image';
import * as style from '../styles/style.module.css';

const Header = () => {
  const { site } = useStaticQuery(query);
  const {
    siteName,
    siteSubTitle,
  } = site.siteMetadata;

  return (
    <>
      <header className={style.header}>
        <div className={style.headerInner}>
          <div className={`${style.signBoard} ${style.wrapper}`}>
            <h1 className={style.siteTitle}>
              {siteName}｜{siteSubTitle}
            </h1>
            <nav>
              <ul className={style.gNav}>
                <li className={style.gNavItem}>
                  <Link to="/" className={style.gNavLink}>ホーム</Link>
                </li>
                <li className={style.gNavItem}>
                  <Link to="/greeting" className={style.gNavLink}>ごあいさつ</Link>
                </li>
                <li className={style.gNavItem}>
                  <Link to="/memo" className={style.gNavLink}>メモリスト</Link>
                </li>
                <li className={style.gNavItem}>
                  <Link to="/tag-list" className={style.gNavLink}>タグリスト</Link>
                </li>
              </ul>
            </nav>
            <div className={style.logoTitle}>
              <Link to="/">
                <StaticImage
                  className={style.logoTitleImg} 
                  alt="ホームページ制作「月の森デザイン」"
                  src="../img/logoTitle.png"
                  placeholder="Blurred"
                />
              </Link>
            </div>
          </div>
        </div>
      </header>
    </>
  )
};

const query = graphql`
  query {
    site {
      siteMetadata {
        siteName
        siteSubTitle
      }
    }
  }
`;

export default Header;
