import * as React from 'react';
import { useStaticQuery, graphql, Link } from 'gatsby';
import * as style from '../styles/style.module.css';

const Footer = () => {
  const { site } = useStaticQuery(query);
  const { siteName } = site.siteMetadata;

  return (
    <>
      <footer className={style.footer}>
        <div className={style.footerInner}>
          <div className={`${style.footerLower} ${style.wrapper}`}>
            <ul className={style.menuSub}>
              <li className={style.menuSubItem}>
                <Link to="/terms-of-service" className={style.menuSubLink}>ご利用規約</Link>
              </li>
              <li className={style.menuSubItem}>
                <Link to="/privacy-policy" className={style.menuSubLink}>プライバシーポリシー</Link>
              </li>
            </ul>
            <div className={style.copyright}>
              <p>
                <small>
                  Copyright &copy; 2021 <Link to="/" className={style.copyrightLink}>{siteName}</Link> All Rights Reserved.
                </small>
              </p>
            </div>
          </div>
        </div>
      </footer>
    </>
  )
};

const query = graphql`
  query {
    site {
      siteMetadata {
        siteName
      }
    }
  }
`;

export default Footer;
